import React from "react";
import { Button, Stack } from "@mui/material";
import { Link } from "react-scroll";
import { useLocation, useNavigate } from "react-router-dom";
import { scroller } from "react-scroll/modules";

const smoothScrollProps = {
  spy: true,
  smooth: true,
  offset: -70,
  duration: 500,
};
const Menu = () => {
  const router = useLocation();
  const navigateTo=useNavigate();
  const navigate =  (section) => {
      if (router.pathname !== "/") {
        navigateTo("/")
        setTimeout(() => scroller.scrollTo(section, { ...smoothScrollProps}), 30);
      }
  };
  return (
    <Stack direction="row" spacing={1}>
      {["Accueil", "Prsentation", "Produits", "Tmoignages", ].map((elem, k) => (
          <Link key={k}{...smoothScrollProps}  onClick={() => navigate(`section${elem}`)} to={"section" + elem}>
       <Button  color="inherit">
          {elem}
        </Button>
        </Link>
      ))}
        <Link {...smoothScrollProps} to={"sectionContact"}>
      <Button variant="contained">Contact</Button>
      </Link>
    </Stack>
  );
};

export default Menu;
